body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@font-face {
  font-family: christmas;
  src: url(PWHappyChristmas.ttf);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* general settings ... */
:root {
  --christmas-color: #D6001C;
  --christmas-tree-color: #00873E;
}
ul {
  list-style-type: none;
}

* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: black;
}

button {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  background-color: var(--christmas-color);
  outline: none;
  border-radius: 5px;
  border: none;
  padding: 5px 10px;
}

button:active,
button:hover {
  transform: scale(1.1);
}

button:active {
  transform: translate(5px, 0px);
}

/* app  */
.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
  overflow: auto;
}

/* navbar  */
.navbar {
  background-color: var(--christmas-color);
  display: flex;
  justify-content: space-between;
  padding: .3rem 2rem;
}

.logo {
  font-family: christmas;
  display: flex;
  align-items: center;
  gap: 1rem;
}

.nav-menu {
  display: flex;
  align-items: center;
  gap: 2rem;
  font-weight: bold;
}

.logo-img {
  border-radius: 100%;
  width: 40px;
  height: 40px;
}

.cart-logo, 
.menu-btn {
  width: 30px;
  height: 30px;
}

.nav-toggled-menu {
  display: grid;
  gap: 1rem;
  padding: 1rem;
  justify-items: center;
  align-items: center;
  font-family: christmas;
  background-color: var(--christmas-color);
}

.nav-toggled-menu > li {
  font-size: 26px;
}

@media screen and (min-width: 790px) {
  .mask {
    justify-content: center;
    align-items: center;
  }
}

@media screen and (max-width: 790px) {
  .mask {
    justify-content: center;
    align-items: flex-start;
  }
}

.home-text,
.home-text-p {
  font-family: christmas;
  background-color: var(--christmas-color);
  padding: 0.5rem 1rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  font-style: italic;
  align-items: center;
  justify-content: center;
}

.home-text-p {
  background-color: transparent;
  font-weight: bold;
  font-size: 50px;
  color: whitesmoke;
  text-shadow: 5px 5px var(--christmas-color);
}

.home {
  flex: 1;
  display: flex;
}

.home-bg {
  background-size: cover;
  width: 100%;
  position: relative;
  flex: 1;
}

.mask {
  height: 100%;
  width: 100%;
  position: absolute;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 1rem;
}
