@media screen and (min-width: 790px) {
  .products {
    width: 70%;
    padding: 1rem;
  }

  .product {
    display: flex;
    flex-direction: column;
    padding: 2rem;
    gap: 1rem;
  }

  .product-img-container {
    height: 280px;
  }
}

@media screen and (max-width: 790px) {
  .products {
    width: 100%;
    gap: 1rem;
    padding: 1rem;
  }

  .product {
    display: flex;
    flex-direction: column;
  }

  .product-text {
    font-size: 11px;
  }

  .products-main {
    flex: 1;
  }

  .product-img-container {
    height: 150px;
  }
}

.product-img-container {
  padding: 0.1rem;
  display: flex;
  justify-content: center;
  background-color: black;
}


.products-main {
  flex: 1;
  display: flex;
  background-color: white;
  justify-content: center;
  padding: 1rem;
}

.products {
  background-color: var(--christmas-color);
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  justify-content: center;
  align-items: center;
  border: solid black 5px;
  border-radius: 10px;
}  

.products-icon {
  width: 10px;
  height: 10px;
}

/* orderlist  */
.order-detail-picture {
  width: 100%;
  height: 100%;
}

/* product  */
.product-pay {
  display: flex;
  justify-content: end;
  gap: 1rem;
}

.product-order-detail {
  display: flex;
  align-content: center;
  gap: .5rem;
}

.product-img {
  width: 100%;
  height: 100%;
}

.product-text {
  text-align: end;
}

.product-detail {
  display: grid;
  flex-direction: column;
  gap: 0.5rem;
  background-color: RGB(240, 240, 240);
  padding: .5rem 1rem;
  border-radius: 10px;
}

.product-cart-button {
  width: 100%;
  font-family: christmas;
}

.product-container {
  display: flex;
  flex-direction: column;
  gap: .5rem;
}

/* OrderDetail */
@media screen and (min-width: 790px) {
  .order-list {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
  }

  .sp {
    display: flex;
    flex-direction: column;
    padding: 4rem;
    flex: 1;
  }

  .order-detail {
    display: flex;
    width: 100%;
    height: 100px;
    gap: 1rem;
    margin-top: 1rem;
  }
}

@media screen and (max-width: 790px) {
  .sp {
    display: flex;
    flex-direction: column;
    padding: 1rem;
    flex: 1;
  }

  .order-list {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    padding: 1rem;
  }

  .order-detail {
    display: grid;
    grid-template-columns: 1fr 4fr;
    gap: 1rem;
    margin-top: 1rem;
  }
}

.order-list{
  border: 5px solid var(--christmas-color);
  padding: 1rem;
  border-radius: 1rem;  padding: 1rem;
}

.order-img-container {
  padding: 5px;
}

.order-detail-texts {
  display: grid;
  align-items: center;
  gap: 0.5rem;
}

/* total price  */
.pay-container {
  display: grid;
  justify-content: center;
  gap: 1rem;
  border: 5px solid var(--christmas-color);
  padding: 1rem;
  border-radius: 1rem;  padding: 1rem;
  width: 50%;
  margin: 0px auto;
  margin-top: 1rem;
}

.shopping-list-empty {
  display: grid;
  gap: 1rem;
  border: 10px solid var(--christmas-color);
  padding: 1rem;
  border-radius: 1rem;
}